import React, { Component } from 'react'


const Message = (props) => {
    const { message } = props

    return ( 
        <div className = "message">
            <h2>{message.title}</h2>
            <p className = "message-body">{message.body}</p>
            <p className = "message-timeRemaining">{getTimeRemainingString(message)}</p>
        </div>
    );
}


function getTimeRemainingString(message) {
    const deleteTime = message.timeToDelete;
    return deleteTime
}

export default Message;
